.app__nav{position:fixed;top:0;left:calc((100vw - 1000px)/2);width:1000px;display:flex;z-index:30;background-color:#000;box-shadow:0 1px 5px #000;border:2px solid #333}.app__nav .app-nav__link{width:25%;text-align:center;display:inline-block;line-height:65px;height:65px;color:#665;font-size:30px;text-decoration:none;background-color:#111;border-left:1px dotted #333;box-shadow:0 0 5px #000 inset;transition:all 150ms;font-family:"BebasNeueRegular",Arial,sans-serif}.app__nav .app-nav__link:hover{color:#ddd}.app__nav .app-nav__link:hover .nav__glyph{color:#eabb00}.app__nav .app-nav__link:hover .cog{transform:rotate(60deg)}.app__nav .app-nav__link:hover .share{transform:rotate(-45deg)}.app__nav .app-nav__link:hover .mail{transform:scale(1.2)}.app__nav .app-nav__link:hover .document_alt_fill{transform:scale(1.2)}.app__nav .app-nav__link.active{color:#eabb00;cursor:default;background-color:#222;text-shadow:0 1px 3px #000;box-shadow:-8px 0 18px rgba(0,0,0,.7) inset,8px 0 18px rgba(0,0,0,.7) inset}.app__nav .app-nav__link.active:hover{color:#eabb00}.app__nav .app-nav__link.active:hover .nav__glyph{color:#ddd}.app__nav .app-nav__link.active .nav__glyph{color:#ddd}.app__nav .nav__glyph{display:inline-block;font-family:"IconicStroke";color:#3a3a3a;margin-right:15px;font-size:30px;position:relative;transition:all 150ms}.app__nav .mail:before{content:"✉"}.app__nav .document_alt_fill:before{content:""}.app__nav .cog:before{content:"⚙"}.app__nav .share:before{content:""}